import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import { useEffect, useState } from "react";
import { useStudentLeaving } from "../../../../hooks/institute/institute-staff-student-api";
import dayjs from "dayjs";
import style from "../../../../JoiningForm/Student/Form/JoinForm.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import FormInput from "../../../Ui/Input/FormInput";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import DownloadToggle from "../../../../StaffTab/LeavingForm/DownloadToggle/DownloadToggle";
import QLoader from "../../../../Loader/QLoader";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import { withCurrentDate } from "../../../../Functions/timeComparison/timeComparison";
import { studentModifyLeavingData3 } from "../../../../pdf/function/pdf-convertor-function";
import { months } from "../../../../constant/constant_value";
import converter from "number-to-words";
import { ymdSlashDateFormat } from "../../../Utils/Functions/dateUtility";
import RaiseStudentCertificateRequestAfterFill from "../../../../MembersTab/student/Mainbody/StudentCertificate/RaiseStudentCertificateRequestAfterFill";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { useCertificateRequestIssueAction } from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";

const PvgStudentLeavingCertificateForm = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [add, setAdd] = useState(false);

  const [step, setStep] = useState({
    isOpen: false,
    isOrigin: false,
    letterHead: false,
  });
  const [state, setState] = useState(leavingState);
  const [errorField, setErrorField] = useState("");

  const [fileUpload] = useFileUpload();
  const [certificateRequestIssueAction] = useCertificateRequestIssueAction();
  const [studentLeaving] = useStudentLeaving();

  useEffect(() => {
    if (getQuery.state?.student) {
      setState((prev) => ({
        ...prev,
        insName: getQuery.state?.student?.institute?.insName ?? "",
        insPhoneNumber:
          getQuery.state?.student?.institute?.insPhoneNumber ?? "",
        insAddress: getQuery.state?.student?.institute?.insAddress ?? "",
        insEmail: getQuery.state?.student?.institute?.insEmail ?? "",
        insAffiliated: getQuery.state?.student?.institute?.insAffiliated ?? "",
        insEditableText_one:
          getQuery.state?.student?.institute?.insEditableText_one ?? "",
        insEditableText_two:
          getQuery.state?.student?.institute?.insEditableText_two ?? "",
        affiliatedImage:
          getQuery.state?.student?.institute?.affliatedLogo ?? "",
        insProfilePhoto:
          getQuery.state?.student?.institute?.insProfilePhoto ?? "",

        studentCertificateNo: getQuery.state?.student?.studentCertificateNo
          ? getQuery.state?.student?.studentCertificateNo
          : `${
              getQuery.state?.student?.institute
                ?.certificate_original_leaving_count + 1
            }`,
        leaving_date: getQuery.state?.student?.leaving_date ?? "",

        leaving_student_name: getQuery.state?.student?.leaving_student_name
          ? getQuery.state?.student?.leaving_student_name
          : `${getQuery.state?.student?.studentFirstName ?? ""} ${
              getQuery.state?.student?.studentMiddleName ?? ""
            } ${getQuery.state?.student?.studentLastName}`,

        lcRegNo: getQuery.state?.student?.lcRegNo
          ? getQuery.state?.student?.lcRegNo
          : getQuery.state?.student?.student_prn_enroll_number ?? "",
        lcCaste: getQuery.state?.student?.lcCaste
          ? getQuery.state?.student?.lcCaste
          : `${getQuery.state?.student?.studentCastCategory ?? ""} - ${
              getQuery.state?.student?.studentCast ?? ""
            }`,

        leaving_nationality: getQuery.state?.student?.leaving_nationality
          ? getQuery.state?.student?.leaving_nationality
          : getQuery.state?.student?.leaving_nationality,
        leaving_religion: getQuery.state?.student?.leaving_religion
          ? getQuery.state?.student?.leaving_religion
          : getQuery.state?.student?.leaving_religion,

        lcBirth: getQuery.state?.student?.lcBirth
          ? getQuery.state?.student?.lcBirth
          : getQuery.state?.student?.studentBirthPlace ?? "",
        lcDOB: getQuery.state?.student?.lcDOB
          ? getQuery.state?.student?.lcDOB
          : getQuery.state?.student?.studentDOB ?? "",

        leaving_previous_school: getQuery.state?.student
          ?.leaving_previous_school
          ? getQuery.state?.student?.leaving_previous_school
          : getQuery.state?.student?.leaving_previous_school,

        lcAdmissionDate: getQuery.state?.student?.lcAdmissionDate
          ? getQuery.state?.student?.lcAdmissionDate === "Invalid Date"
            ? ""
            : getQuery.state?.student?.lcAdmissionDate
          : dayjs(getQuery.state?.student?.studentAdmissionDate).format(
              "YYYY-MM-DD"
            ),
        studentLeavingBehaviour:
          getQuery.state?.student?.studentLeavingBehaviour ?? "",
        lcInstituteDate: getQuery.state?.student?.lcInstituteDate
          ? getQuery.state?.student?.lcInstituteDate
          : dayjs(getQuery.state?.student?.studentLeavingInsDate).format(
              "YYYY-MM-DD"
            ),
        studentLeavingStudy: getQuery.state?.student?.studentLeavingStudy ?? "",
        leaving_degree: getQuery.state?.student?.leaving_degree ?? "",
        leaving_since_date: getQuery.state?.student?.leaving_since_date ?? "",
        leaving_course_duration: getQuery.state?.student
          ?.leaving_course_duration
          ? getQuery.state?.student?.leaving_course_duration
          : getQuery.state?.student?.studentMotherName ?? "",
        studentLeavingReason:
          getQuery.state?.student?.studentLeavingReason ?? "",
        elective_subject_one:
          getQuery.state?.student?.elective_subject_one ?? "",
        elective_subject_second:
          getQuery.state?.student?.elective_subject_second ?? "",
        leaving_project_work:
          getQuery.state?.student?.leaving_project_work ?? "",
        leaving_guide_name: getQuery.state?.student?.leaving_guide_name ?? "",
        studentRemark: getQuery.state?.student?.studentLeavingRemark ?? "",
        whichCase: "other",
      }));
      setStep((prev) => ({
        ...prev,
        isOrigin: getQuery.state?.student?.certificateLeavingCopy?.originalCopy,
      }));
    }
  }, [getQuery.state?.student]);

  const onClose = () => {
    setAdd((pre) => !pre);
  };

  const onDateChange = (val, label) => {
    if (val) {
      // let splitDate = val?.split("/");
      // let dob = `${splitDate[2]}-${splitDate[1]}-${splitDate[0]}`;
      setState((prevState) => ({
        ...prevState,
        [label]: ymdSlashDateFormat(val),
      }));
    }
  };
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onDateFunction = (val) => {
    // console.info("Sfhgsdfs,", val);
    const dobCorrect = withCurrentDate(val);
    if (dobCorrect) {
      let splitDate = val?.split("-");
      let dob = `${splitDate[2]}-${splitDate[1]}-${splitDate[0]}`;
      setState((prevState) => ({
        ...prevState,
        lcDOB: dob,
      }));
    } else {
    }
  };
  const onTile = (value) => {
    setState((prev) => ({
      ...prev,
      whichCase: value,
    }));
  };
  const onLetterHead = () => {
    setStep((prev) => ({
      ...prev,
      letterHead: true,
    }));
  };

  const onSavaData = () => {
    let validation = requiredField(state, [
      "insAffiliated",
      "insEditableText_one",
      "insEditableText_two",
      "affiliatedImage",
      "insProfilePhoto",

      "leaving_religion",
      "leaving_degree",
      "leaving_since_date",
      "elective_subject_one",
      "elective_subject_second",
      "leaving_guide_name",
      "whichCase",
    ]);
    if (existingRequiredField(validation)) {
      setDisabled((pre) => !pre);
      if (getQuery.state?.extraData?.accessFrom === "Student") {
        studentLeaving({
          id: getQuery.state?.student?.institute?._id,
          gr: getQuery.state?.student?._id,
          leavingFeedback: {
            number: getQuery.state?.student?.studentGRNO,
            ...state,
            leaving_certificate_attach: "",
            accessFrom: "Student",
          },
        })
          .then(() => {
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      } else {
        studentLeaving({
          id: getQuery.state?.student?.institute?._id,
          gr: getQuery.state?.student?.studentGRNO,
          leavingFeedback: {
            number: getQuery.state?.student?.studentGRNO,
            ...state,
            leaving_certificate_attach: "",
          },
        })
          .then(() => {
            setDisabled((pre) => !pre);
            navigate(-1);
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  const generateLeavingPDF = () => {
    let validation = requiredField(state, [
      "insAffiliated",
      "insEditableText_one",
      "insEditableText_two",
      "affiliatedImage",
      "insProfilePhoto",

      "leaving_religion",
      "leaving_degree",
      "leaving_since_date",
      "elective_subject_one",
      "elective_subject_second",
      "leaving_guide_name",
      "whichCase",
    ]);
    if (existingRequiredField(validation)) {
      setDisabled((pre) => !pre);
      const { institute, studentInfo } = studentModifyLeavingData3(state, {
        isOriginal: step.isOrigin,
        is_leaving: getQuery.state?.is_leaving,
        is_letterhead: step.letterHead,
      });
      // console.info("studentInfo", studentInfo);
      const workerInstance = new Worker(
        new URL(
          "../../../../pdf/worker/main.certificate.pdf.worker.js",
          import.meta.url
        )
      );
      workerInstance.postMessage({
        printCertificateType: "LEAVING_3",
        studentInfo: studentInfo,
        institute: institute,
      });

      workerInstance.onmessage = (message) => {
        if (message) {
          if (getQuery.state?.isViewBy === "REQUEST_TAB") {
            const formData = new FormData();
            formData.append("file", message.data);

            fileUpload({
              fileUpload: formData,
            })
              .then((res) => {
                studentLeaving({
                  id: getQuery.state?.student?.institute?._id,
                  gr: getQuery.state?.student?.studentGRNO,
                  leavingFeedback: {
                    number: getQuery.state?.student?.studentGRNO,
                    ...state,
                  },
                })
                  .then(() => {})
                  .catch({});

                certificateRequestIssueAction({
                  cid: getQuery.state?.certificateId,
                  bodyParameter: {
                    attach: res.data?.imageKey,
                    status: "Issued",
                    certificate_type: getQuery.state?.is_leaving,
                    certificate_attachment: res.data?.imageKey,
                    student_name: studentInfo?.name,
                    staffId: getQuery.state?.staffId,
                    is_dublicate: !studentInfo?.isOriginal,
                    certificate_original_leaving_count:
                      state?.studentCertificateNo,
                    other_data: {
                      number: getQuery.state?.student?.studentGRNO,
                      ...state,
                    },
                  },
                })
                  .then(() => {
                    setDisabled((pre) => !pre);
                    navigate(-1);
                    window.open(URL.createObjectURL(message.data), "_blank");
                  })
                  .catch({});
              })
              .catch({});
          } else {
            const formData = new FormData();
            formData.append("file", message.data);
            fileUpload({
              fileUpload: formData,
            })
              .then((res) => {
                studentLeaving({
                  id: getQuery.state?.student?.institute?._id,
                  gr: getQuery.state?.student?.studentGRNO,
                  leavingFeedback: {
                    number: getQuery.state?.student?.studentGRNO,
                    ...state,
                    leaving_certificate_attach: res.data?.imageKey,
                    certificate_type: getQuery.state?.is_leaving,
                    certificate_attachment: res.data?.imageKey,
                    student_name: studentInfo?.name,
                    staffId: getQuery.state?.staffId,
                    is_dublicate: !studentInfo?.isOriginal,
                    certificate_original_leaving_count:
                      state?.studentCertificateNo,
                  },
                })
                  .then(() => {
                    window.open(URL.createObjectURL(message.data), "_blank");
                    // window.open(message.data, "_blank");
                    setDisabled((pre) => !pre);
                    navigate(-1);
                  })
                  .catch({});
              })
              .catch({});
          }
        }
      };
    } else {
      setErrorField(validation);
    }
  };

  // console.info("Sbfhdsvfs", getQuery.state);
  return (
    <>
      {getQuery.state?.extraData?.accessFrom === "Student" ? null : (
        <>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <h6 className={style.student_from_fill}>
              {t("fill_certificate_form")}
              {getQuery.state?.student?.duplicate_copy === "Original Copy" ? (
                <span>({t("original_not_given")})</span>
              ) : (
                <span>({t("original_given")})</span>
              )}
            </h6>
          </div>
          <div
            className={style.form_profile_info}
            style={{
              paddingInline: "1rem",
            }}
          >
            <div
              style={{
                width: "88%",
              }}
            >
              <FormInput
                label={t("institute_name")}
                placeholder={t("institute_name")}
                name={"insName"}
                value={state.insName}
                type={"text"}
                onChange={onInputChange}
                customFormInputLabel={{
                  marginTop: "7px",
                  marginBottom: "4px",
                }}
                errorShow={errorField["insName"]}
              />
              <FormInput
                label={t("institute_address")}
                placeholder={t("institute_address")}
                name={"insAddress"}
                value={state.insAddress}
                type={"text"}
                onChange={onInputChange}
                customFormInputLabel={{
                  marginTop: "7px",
                  marginBottom: "4px",
                }}
                errorShow={errorField["insAddress"]}
              />
              <div className={style.form_profile_info}>
                <FormInput
                  label={t("contact_number")}
                  placeholder={t("contact_number")}
                  name={"insPhoneNumber"}
                  value={state.insPhoneNumber}
                  type={"tel"}
                  onChange={onInputChange}
                  customFormInputLabel={{
                    marginTop: "7px",
                    marginBottom: "4px",
                  }}
                  customFormInputContainer={{
                    width: "49%",
                  }}
                  errorShow={errorField["insPhoneNumber"]}
                />
                <FormInput
                  label={t("email")}
                  placeholder={t("email")}
                  name={"insEmail"}
                  value={state.insEmail}
                  type={"text"}
                  onChange={onInputChange}
                  customFormInputLabel={{
                    marginTop: "7px",
                    marginBottom: "4px",
                  }}
                  customFormInputContainer={{
                    width: "49%",
                  }}
                  errorShow={errorField["insEmail"]}
                />
              </div>
            </div>
            <div className={style.profilephotocontainer}>
              <img
                className={style.profilephotoins}
                src={
                  state.affiliatedImage
                    ? `${imageShowUrl}/${state.affiliatedImage}`
                    : "/images/ins_default_profile.svg"
                }
                alt="ChatAvatar"
              />
            </div>
          </div>
          <div
            style={{
              paddingInline: "1rem",
            }}
          >
            <FormInput
              label={t("aff_body")}
              placeholder={t("aff_body")}
              name={"insAffiliated"}
              value={state.insAffiliated}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
            />

            <FormInput
              label={t("accreditation")}
              placeholder="Accredited by NAAC with B++, ISO 9001:2015 Certified"
              name={"insEditableText_one"}
              value={state.insEditableText_one}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
            />
          </div>
          <BorderBottom
            customStyleBorder={{
              marginTop: "0.7rem",
            }}
          />
          <div
            className={style.form_profile_info}
            style={{
              paddingInline: "1rem",
            }}
          >
            <FormInput
              label={t("certificate_no")}
              placeholder={t("certificate_no")}
              name={"studentCertificateNo"}
              value={state.studentCertificateNo}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "49%",
              }}
              errorShow={errorField["studentCertificateNo"]}
            />
            <FormCalendarInput
              label={t("date")}
              placeholder={t("date_placeholder")}
              name={"leaving_date"}
              value={state.leaving_date}
              onChange={(data) => onDateChange(data, "leaving_date")}
              customFormInputContainer={{ position: "relative", width: "49%" }}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              errorShow={errorField["leaving_date"]}
              isNotPassCurrentDate
            />
          </div>
        </>
      )}

      <div
        className={style.certidyheading}
        style={{
          fontSize: "18px",
        }}
      >
        {t(
          getQuery.state?.is_leaving === "MTC"
            ? "mtc_certificate"
            : getQuery.state?.is_leaving === "MLC"
            ? "mlc_certificate"
            : "transfer_leaving_certificate"
        )}
      </div>

      <div
        style={{
          paddingInline: "1rem",
        }}
      >
        <div className={style.form_profile_info}>
          <FormInput
            label={t("name")}
            placeholder={t("name")}
            name={"leaving_student_name"}
            value={state.leaving_student_name}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            errorShow={errorField["leaving_student_name"]}
          />
          <FormInput
            label={t("mother_name")}
            placeholder={t("mother_name_placeholder")}
            name={"leaving_course_duration"}
            value={state.leaving_course_duration}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              width: "49%",
            }}
            errorShow={errorField["leaving_course_duration"]}
          />
        </div>

        <div className={style.form_profile_info}>
          <FormInput
            label={t("register_number_of_candidate")}
            placeholder={t("register_number_of_candidate_placeholder")}
            name={"lcRegNo"}
            value={state.lcRegNo}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              outline: "none",
            }}
            errorShow={errorField["lcRegNo"]}
          />
          <FormInput
            label={t("cast")}
            placeholder={t("cast")}
            name={"lcCaste"}
            value={state.lcCaste}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              width: "49%",
            }}
            errorShow={errorField["lcCaste"]}
          />
        </div>
        <div className={style.form_profile_info}>
          <div
            className={style.form_profile_info}
            style={{
              width: "49%",
            }}
          >
            <CustomSelect
              selectLabel={t("nationality")}
              selectedValue={t("nationality")}
              defalutValue={state.leaving_nationality}
              name={"leaving_nationality"}
              onClick={(val) =>
                onInputChange({
                  target: {
                    name: "leaving_nationality",
                    value: val,
                  },
                })
              }
              options={["Indian", "Non-resident Indian"]}
              customLabelStyle={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customStyleContainer={{
                width: "30%",
              }}
              customListStyle={{
                position: "absolute",
                zIndex: "12",
                width: "100%",
              }}
              errorShow={errorField["leaving_nationality"]}
            />
            <FormInput
              label={t("other")}
              placeholder={t("please_type_here")}
              name={"leaving_nationality"}
              value={state.leaving_nationality}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "69%",
              }}
            />
          </div>
          <FormInput
            label={t("place_of_birth")}
            placeholder={t("place_of_birth_placeholder")}
            name={"lcBirth"}
            value={state.lcBirth}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              width: "49%",
            }}
            errorShow={errorField["lcBirth"]}
          />
        </div>
        <div className={style.form_profile_info}>
          <FormCalendarInput
            label={t("dob")}
            placeholder={t("dob")}
            name={"lcDOB"}
            value={state.lcDOB}
            onChange={onDateFunction}
            customFormInputContainer={{ position: "relative", width: "49%" }}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            errorShow={errorField["lcDOB"]}
            isNotPassCurrentDate
          />
          <FormInput
            label={t("in_words")}
            placeholder={t("in_words")}
            name={"studentDOBWork"}
            value={
              state.lcDOB
                ? `${toPascalCase(
                    converter.toWords(
                      state?.lcDOB?.split("T")[0]?.split("-")?.[2]
                    )
                  )}  ${
                    months[state?.lcDOB?.split("T")[0]?.split("-")?.[1] - 1]
                  } ${toPascalCase(
                    converter.toWords(
                      state?.lcDOB?.split("T")[0]?.split("-")?.[0]
                    )
                  )}`
                : ""
            }
            type={"text"}
            onChange={() => {}}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              width: "49%",
            }}
          />
        </div>
        <div className={style.form_profile_info}>
          <FormInput
            label={t("institute_last_attend")}
            placeholder={t("institute_last_attend_placeholder")}
            name={"leaving_previous_school"}
            value={state.leaving_previous_school}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              width: "49%",
            }}
            errorShow={errorField["leaving_previous_school"]}
          />
          <FormCalendarInput
            label={t("date_of_admission")}
            placeholder={t("date_of_admission_placeholder")}
            name={"lcAdmissionDate"}
            value={state.lcAdmissionDate}
            onChange={(data) => onDateChange(data, "lcAdmissionDate")}
            customFormInputContainer={{ position: "relative", width: "49%" }}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            errorShow={errorField["lcAdmissionDate"]}
            isNotPassCurrentDate
          />
        </div>

        <div className={style.form_profile_info}>
          <div
            className={style.form_profile_info}
            style={{
              width: "49%",
            }}
          >
            <CustomSelect
              selectLabel={t("progress")}
              selectedValue={t("progress")}
              defalutValue={state.leaving_project_work}
              name={"leaving_project_work"}
              onClick={(val) =>
                onInputChange({
                  target: {
                    name: "leaving_project_work",
                    value: val,
                  },
                })
              }
              options={["Beginner", "Intermediate", "Advanced"]}
              customLabelStyle={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customStyleContainer={{
                width: "30%",
              }}
              customListStyle={{
                position: "absolute",
                zIndex: "12",
                width: "100%",
              }}
              errorShow={errorField["leaving_project_work"]}
            />
            <FormInput
              label={t("other")}
              placeholder={t("please_type_here")}
              name={"leaving_project_work"}
              value={state.leaving_project_work}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "69%",
              }}
            />
          </div>
          <div
            className={style.form_profile_info}
            style={{
              width: "49%",
            }}
          >
            <CustomSelect
              selectLabel={t("conduct")}
              selectedValue={t("conduct_placeholder")}
              defalutValue={state.studentLeavingBehaviour}
              name={"studentLeavingBehaviour"}
              onClick={(val) =>
                onInputChange({
                  target: {
                    name: "studentLeavingBehaviour",
                    value: val,
                  },
                })
              }
              options={["Excellent", "Very Good", "Good", "Bad", "Very Bad"]}
              customLabelStyle={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customStyleContainer={{
                width: "30%",
              }}
              customListStyle={{
                position: "absolute",
                zIndex: "12",
                width: "100%",
              }}
              errorShow={errorField["studentLeavingBehaviour"]}
            />
            <FormInput
              label={t("other")}
              placeholder={t("please_type_here")}
              name={"studentLeavingBehaviour"}
              value={state.studentLeavingBehaviour}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "69%",
              }}
            />
          </div>
        </div>
        <div className={style.form_profile_info}>
          <FormCalendarInput
            label={t("date_of_leaving_institute")}
            placeholder={t("date_of_leaving_institute_placeholder")}
            name={"lcInstituteDate"}
            value={state.lcInstituteDate}
            onChange={(data) => onDateChange(data, "lcInstituteDate")}
            customFormInputContainer={{ position: "relative", width: "49%" }}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            errorShow={errorField["lcInstituteDate"]}
            isNotPassCurrentDate
          />
          <FormInput
            label={t("year_in_which_studying_since_when")}
            placeholder={t("since_placeholder")}
            name={"studentLeavingStudy"}
            value={state.studentLeavingStudy}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              width: "49%",
            }}
            errorShow={errorField["studentLeavingStudy"]}
          />
        </div>

        <div className={style.form_profile_info}>
          <FormInput
            label={t("reason_for_leaving_institute")}
            placeholder={t("reason_for_leaving_institute_placeholder")}
            name={"studentLeavingReason"}
            value={state.studentLeavingReason}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
            customFormInputContainer={{
              width: "49%",
            }}
            errorShow={errorField["studentLeavingReason"]}
          />
        </div>

        <div className={style.form_profile_info}>
          <div
            className={style.form_profile_info}
            style={{
              width: "100%",
            }}
          >
            <CustomSelect
              selectLabel={t("remarks")}
              selectedValue={t("remarks")}
              defalutValue={state.studentRemark}
              name={"studentRemark"}
              onClick={(val) =>
                onInputChange({
                  target: {
                    name: "studentRemark",
                    value: val,
                  },
                })
              }
              options={["Parents Wish", "Course Completed"]}
              customLabelStyle={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customStyleContainer={{
                width: "30%",
              }}
              customListStyle={{
                position: "absolute",
                zIndex: "12",
                width: "100%",
              }}
              errorShow={errorField["studentRemark"]}
            />

            <FormInput
              label={t("other")}
              placeholder={t("please_type_here")}
              name={"studentRemark"}
              value={state.studentRemark}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "69%",
              }}
            />
          </div>
        </div>
      </div>
      {getQuery.state?.extraData?.accessFrom === "Student" ? null : (
        <div
          className={style.form_profile_info}
          style={{
            justifyContent: "flex-start",
            alignItems: "flex-start",
            gap: "1.5rem",
          }}
        >
          <TileWrapper
            label="select_print_case"
            customTileStyle={{
              marginLeft: "1rem",
            }}
          >
            {whichCaseMode?.map((tile) => (
              <TileCard
                key={tile?.key}
                tile={tile}
                onTile={onTile}
                activeTile={state.whichCase}
              />
            ))}
          </TileWrapper>
          <section className={style.letter_head_container}>
            {step.letterHead ? (
              <img
                src="/images/checkbox-icon.svg"
                alt="icon"
                onClick={() =>
                  setStep((prev) => ({
                    ...prev,
                    letterHead: false,
                  }))
                }
              />
            ) : (
              <img
                src="/images/box-icon.svg"
                alt="icon"
                onClick={onLetterHead}
              />
            )}

            <h6>{t("printing_on_letter_head")}</h6>
          </section>
        </div>
      )}

      {getQuery.state?.extraData?.accessFrom === "Student" ? (
        <div className={style.buttoncontainer}>
          <div
            className={style.buttonInner}
            style={{
              width: "100%",
            }}
          >
            <div
              className={style.button_main}
              style={{
                marginTop: "1.5vw",
                width: "100%",
                cursor: "pointer",
                backgroundColor: "#001a72",
              }}
              onClick={onSavaData}
            >
              <span>{t("save")}</span>
            </div>
          </div>
        </div>
      ) : (
        <div className={style.buttoncontainer}>
          <div
            className={style.buttonInner}
            style={{
              width: "32%",
            }}
          >
            {step.isOrigin ? (
              <div
                className={style.button_main}
                style={{
                  marginTop: "1.5vw",
                  width: "100%",
                  cursor: "pointer",
                  backgroundColor: "#4CAF50",
                }}
                onClick={() =>
                  setStep((prev) => ({
                    ...prev,
                    isOpen: true,
                  }))
                }
              >
                <span>{t("original_copy")}</span>
              </div>
            ) : (
              <div
                className={style.button_main}
                style={{
                  marginTop: "1.5vw",
                  width: "100%",
                  cursor: "pointer",
                  backgroundColor: "rgb(228, 72, 121)",
                }}
                onClick={() =>
                  setStep((prev) => ({
                    ...prev,
                    isOpen: true,
                  }))
                }
              >
                <span>{t("duplicate_copy")}</span>
              </div>
            )}
          </div>
          <div
            className={style.buttonInner}
            style={{
              width: "32%",
            }}
          >
            <div
              className={style.button_main}
              style={{
                marginTop: "1.5vw",
                width: "100%",
                cursor: "pointer",
                backgroundColor: "#001a72",
              }}
              onClick={onSavaData}
            >
              <span>{t("save")}</span>
            </div>
          </div>
          <div
            className={style.buttonInner}
            style={{
              width: "32%",
            }}
          >
            <div
              className={style.button_main}
              style={{
                marginTop: "1.5vw",
                width: "100%",
                cursor: "pointer",
              }}
              onClick={generateLeavingPDF}
            >
              <span>{t("download_certificate")}</span>
            </div>
          </div>
        </div>
      )}

      {step.isOpen && (
        <DownloadToggle
          open={step.isOpen}
          hideModal={() =>
            setStep((prev) => ({
              ...prev,
              isOpen: false,
            }))
          }
          insId={getQuery.state?.student?.institute?._id}
          setIsOrigin={() =>
            setStep((prev) => ({
              ...prev,
              isOrigin: true,
            }))
          }
        />
      )}
      {disabled && <QLoader />}

      {add && (
        <RaiseStudentCertificateRequestAfterFill
          studentId={getQuery.state?.student?._id}
          instituteId={getQuery.state?.instituteId}
          onClose={onClose}
          whichTab={getQuery.state?.extraData?.whichTab}
        />
      )}
    </>
  );
};

export default PvgStudentLeavingCertificateForm;
const toPascalCase = (str) =>
  (str.match(/[a-zA-Z0-9]+/g) || [])
    .map((w) => `${w.charAt(0).toUpperCase()}${w.slice(1)}`)
    .join(" ");
