import { useLocation } from "react-router-dom";
import NavbarConfig from "../../../Shared/Navbar/NavbarConfig";
import GoBack from "../../../Shared/StaffStudentProfile/GoBack";
import StaffStudentProfileContainer from "../../../Shared/StaffStudentProfile/StaffStudentProfileContainer";
// import StudentLeavingCertificateFormNotExist from "../../../../MembersTab/staff/Mainbody/CertificateAuthority/StudentLeavingCertificateFormNotExist";
// import StudentLeavingCertificateFormPVGNotExist from "../../../../MembersTab/staff/Mainbody/CertificateAuthority/StudentLeavingCertificateFormPVGNotExist";
// import StudentLeavingCertificateForm3 from "../../../../MembersTab/staff/Mainbody/CertificateAuthority/StudentLeavingCertificateForm3";
// import StudentLeavingCertificateForm from "../../../../MembersTab/staff/Mainbody/CertificateAuthority/StudentLeavingCertificateForm";
import SpceStudentLeavingCertificateForm from "./SpceStudentLeavingCertificateForm";
import SpceLeavingCertificateFormNotExist from "./SpceLeavingCertificateFormNotExist";
import PvgStudentLeavingCertificateFormNotExist from "./PvgStudentLeavingCertificateFormNotExist";
import PvgStudentLeavingCertificateForm from "./PvgStudentLeavingCertificateForm";
import StandardStudentLeavingCertificateForm from "./StandardStudentLeavingCertificateForm";
import BonafideStudentCertificateForm from "../Bonafide/BonafideStudentCertificateForm";

const StudentCertificateConfig = () => {
  const getQuery = useLocation();
  return (
    <>
      <NavbarConfig
        isView={
          getQuery.state?.whichUrl === "INSTITUTE"
            ? "INSTITUTE"
            : getQuery.state?.viewAs
        }
      >
        <GoBack />
        {getQuery.state?.is_bonafide ? (
          <BonafideStudentCertificateForm />
        ) : (
          <StaffStudentProfileContainer>
            {getQuery.state?.whichCertificate === "NOT_EXIST" ? (
              <>
                {getQuery.state?.student?.institute
                  ?.leave_certificate_selection === "SPCE" ? (
                  <SpceLeavingCertificateFormNotExist />
                ) : getQuery.state?.student?.institute
                    ?.leave_certificate_selection === "PVG College" ? (
                  <PvgStudentLeavingCertificateFormNotExist />
                ) : null}
              </>
            ) : getQuery.state?.student?.institute
                ?.leave_certificate_selection === "SPCE" ? (
              <SpceStudentLeavingCertificateForm />
            ) : getQuery.state?.student?.institute
                ?.leave_certificate_selection === "PVG College" ? (
              <PvgStudentLeavingCertificateForm />
            ) : (
              <StandardStudentLeavingCertificateForm />
            )}
            {/* <BorderBottom /> */}
          </StaffStudentProfileContainer>
        )}
      </NavbarConfig>
    </>
  );
};

export default StudentCertificateConfig;
