import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import FormFile from "../../../Ui/Input/FormFile";
import dayjs from "dayjs";
import {
  useCertificateBonafideStudentDetail,
  useCertificateBonafideStudentUpdateDetail,
  useCertificateRequestIssueAction,
} from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import style from "../../FinanceManager/FinanceManager.module.css";
import StaffStudentProfileContainer from "../../../Shared/StaffStudentProfile/StaffStudentProfileContainer";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import { studentModifyBonafideData } from "../../../../pdf/function/pdf-convertor-function";
import { Dialog } from "@mui/material";
import CustomCrop from "../../../../ImageCrop/CustomCrop";
import QvipleLoader from "../../../Loader/QvipleLoader";
// import { imageShowUrl1 } from "../../../../services/BaseUrl";
const BonafideStudentCertificateForm = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState({
    letterHead: false,
  });
  const [state, setState] = useState({
    name: "",
    reason: "",
    message: "",
    attachment: "",
    is_letterhead: "",
    grnumber: "",
    print_content: "",
    update_date: dayjs().format("YYYY-MM-DD"),
    certificate_number: 1,
    competent_authority: "",
  });

  const [openCrop, setOpenCrop] = useState(false);
  const [fileUrl, setFileUrl] = useState("");

  const [fileUpload] = useFileUpload();
  const [certificateBonafideStudentUpdateDetail] =
    useCertificateBonafideStudentUpdateDetail();
  const [certificateRequestIssueAction] = useCertificateRequestIssueAction();

  const {
    certificateBonafideStudentDetail,
    certificateBonafideStudentDetailLoading,
    certificateBonafideStudentDetailRefetch,
  } = useCertificateBonafideStudentDetail({
    data: {
      sid: getQuery.state?.studentId,
    },
    skip: !getQuery.state?.studentId,
  });
  useEffect(() => {
    if (certificateBonafideStudentDetail?._id) {
      let ct = "This is to certify that Mr./Miss @";
      let nt = "";
      nt += `${certificateBonafideStudentDetail?.studentFirstName} `;
      nt += certificateBonafideStudentDetail?.studentMiddleName
        ? `${certificateBonafideStudentDetail?.studentMiddleName} `
        : "";
      nt += certificateBonafideStudentDetail?.studentLastName;

      ct += nt;
      ct += "@, Birth date as per records of institute is @";
      ct += certificateBonafideStudentDetail?.studentDOB
        ? `${certificateBonafideStudentDetail?.studentDOB}`
        : "N/A";
      ct += "@, is a bona-fide student of this institute, studying in class @";
      ct += certificateBonafideStudentDetail?.studentClass?.classTitle
        ? `${certificateBonafideStudentDetail?.studentClass?.classTitle},`
        : "N/A,";
      ct += certificateBonafideStudentDetail?.department?.dName
        ? ` ${certificateBonafideStudentDetail?.department?.dName},`
        : " N/A,";
      ct += certificateBonafideStudentDetail?.batches?.batchName
        ? ` ${certificateBonafideStudentDetail?.batches?.batchName}@.`
        : " N/A@.";

      let ct_number =
        certificateBonafideStudentDetail?.institute
          ?.certificate_bonafide_count ?? 0;
      ct_number += 1;
      if (certificateBonafideStudentDetail?.student_bonafide?.print_content) {
        ct = certificateBonafideStudentDetail?.student_bonafide?.print_content;
      } else {
      }
      setState((prev) => ({
        ...prev,
        name: nt ?? "",
        reason:
          certificateBonafideStudentDetail?.student_bonafide?.reason ?? "",
        message:
          certificateBonafideStudentDetail?.student_bonafide?.message ?? "",
        attachment:
          certificateBonafideStudentDetail?.student_bonafide?.attachment ?? "",
        is_letterhead:
          certificateBonafideStudentDetail?.student_bonafide?.is_letterhead ??
          "",
        grnumber:
          certificateBonafideStudentDetail?.student_bonafide?.grnumber ??
          certificateBonafideStudentDetail?.studentGRNO ??
          "",
        print_content: ct,
        certificate_number: ct_number,
        competent_authority:
          certificateBonafideStudentDetail?.institute?.authority ?? "",
      }));
    }
  }, [certificateBonafideStudentDetail]);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCropFileUpload = (e) => {
    if (e.target.files?.[0]) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFileUrl(image);
      setOpenCrop(true);
    }
  };

  const onCrop = (fl, url) => {
    setOpenCrop(false);
    onFileUploadCrop(fl);
  };
  const onFileUploadCrop = (data) => {
    if (data) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", data);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            attachment: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onGeneratePdf = () => {
    setDisabled((pre) => !pre);

    const { institute, studentInfo } = studentModifyBonafideData(
      certificateBonafideStudentDetail?.institute,
      certificateBonafideStudentDetail,
      {
        reason: state.reason,
        message: state.message,
        authority: state.competent_authority,
        uniqueGr: state.grnumber,
        letterHead: step.letterHead,
        certificate_bonafide_count: state.certificate_number,
        print_content: state.print_content,
      }
    );

    const workerInstance = new Worker(
      new URL(
        "../../../../pdf/worker/main.certificate.pdf.worker.js",
        import.meta.url
      )
    );
    let ot = fileUrl ? URL.createObjectURL(fileUrl) : "";
    // : `${imageShowUrl1}/${certificateBonafideStudentDetail?.student_bonafide?.attachment}`;
    workerInstance.postMessage({
      printCertificateType: "BONAFIDE",
      studentInfo: studentInfo,
      institute: institute,
      otherImageUrl: ot,
    });

    workerInstance.onmessage = (message) => {
      if (message && certificateBonafideStudentDetail?._id) {
        if (getQuery.state?.isViewBy === "REQUEST_TAB") {
          setDisabled(true);
          const formData = new FormData();
          formData.append("file", message.data);
          fileUpload({
            fileUpload: formData,
          })
            .then((res) => {
              certificateRequestIssueAction({
                cid: getQuery.state?.certificateId,
                bodyParameter: {
                  attach: res.data?.imageKey,
                  status: "Issued",
                  is_bonafide: true,
                  certificate_type: "BONAFIED",
                  certificate_attachment: res.data?.imageKey,
                  student_name: studentInfo?.name,
                  staffId: getQuery.state?.staffId,
                  certificate_bonafide_count: state.certificate_number,
                  instituteId: certificateBonafideStudentDetail?.institute?._id,
                  student_bonafide: state,
                },
              })
                .then(() => {
                  window.open(URL.createObjectURL(message.data), "_blank");
                  setDisabled((pre) => !pre);
                  navigate(-1);
                })
                .catch({});
            })
            .catch({});
        } else {
          const formData = new FormData();
          formData.append("file", message.data);
          fileUpload({
            fileUpload: formData,
          })
            .then((res) => {
              certificateBonafideStudentUpdateDetail({
                sid: certificateBonafideStudentDetail?._id,
                bodyParameter: {
                  isLogs: true,
                  certificate_type: "BONAFIED",
                  certificate_attachment: res.data?.imageKey,
                  student_name: studentInfo?.name,
                  staffId: getQuery.state?.staffId,
                  certificate_bonafide_count: state.certificate_number,
                  instituteId: certificateBonafideStudentDetail?.institute?._id,
                  student_bonafide: state,
                },
              })
                .then(() => {
                  window.open(URL.createObjectURL(message.data), "_blank");
                  setDisabled((pre) => !pre);
                  navigate(-1);
                })
                .catch({});
            })
            .catch({});
        }
      }
    };
  };

  const onSave = () => {
    setDisabled((pre) => !pre);
    certificateBonafideStudentUpdateDetail({
      sid: certificateBonafideStudentDetail?._id,
      bodyParameter: {
        student_bonafide: state,
      },
    })
      .then(() => {
        certificateBonafideStudentDetailRefetch();
        setDisabled((pre) => !pre);
      })
      .catch({});
  };
  // console.info("sdfbsjf", getQuery.state);

  return (
    <>
      <StaffStudentProfileContainer>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("fill_bonafide_form")}</h6>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.fm_header_container}>
            <FormInput
              label={t("certificate_no")}
              placeholder={t("certificate_no")}
              name={"certificate_number"}
              value={state.certificate_number}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "32%",
              }}
            />
            <FormInput
              label={t("name")}
              placeholder={t("please_enter_student_name")}
              name={"name"}
              value={state.name}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "32%",
              }}
            />
            <FormInput
              label={t("grnumber")}
              placeholder={t("please_enter_grnumber")}
              name={"grnumber"}
              value={state.grnumber}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "32%",
              }}
            />
          </div>
          <FormInput
            label={t("enter_reason")}
            placeholder={t("enter_reason")}
            name={"reason"}
            value={state.reason}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
          />

          <FormInput
            label={t("bonafide_message_optional")}
            placeholder={t("bonafide_message_optional_placeholder")}
            name={"message"}
            value={state.message}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
          />

          <FormTextarea
            label={t("bonafide_print_content")}
            placeholder={t("bonafide_print_content_placeholder")}
            name={"print_content"}
            value={state.print_content}
            type={"text"}
            onChange={onInputChange}
            customFormInputLabel={{
              marginTop: "7px",
              marginBottom: "4px",
            }}
          />
          <div className={style.fm_header_container}>
            <FormInput
              label={t("competent_authority")}
              placeholder={t("competent_authority_lable")}
              name={"competent_authority"}
              value={state.competent_authority}
              type={"text"}
              onChange={onInputChange}
              customFormInputLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormInputContainer={{
                width: "49%",
              }}
            />
            <FormFile
              label={t("attachment")}
              name={"attachment"}
              value={
                state.attachment
                  ? {
                      key: state.attachment,
                    }
                  : ""
              }
              type={"text"}
              onChange={onCropFileUpload}
              customFormFileLabel={{
                marginTop: "7px",
                marginBottom: "4px",
              }}
              customFormFileContainer={{
                width: "49%",
              }}
            />
          </div>
          <section
            className={style.fm_header_container}
            style={{
              justifyContent: "flex-start",
              gap: "1rem",
              marginTop: "1.2rem",
            }}
          >
            {step.letterHead ? (
              <img
                src="/images/checkbox-icon.svg"
                alt="icon"
                onClick={() =>
                  setStep((prev) => ({
                    ...prev,
                    letterHead: false,
                  }))
                }
              />
            ) : (
              <img
                src="/images/box-icon.svg"
                alt="icon"
                onClick={() =>
                  setStep((prev) => ({
                    ...prev,
                    letterHead: true,
                  }))
                }
              />
            )}

            <h6>{t("printing_on_letter_head")}</h6>
          </section>

          <div className={style.fm_header_container}>
            <CreateButton
              label="save"
              customStyle={{
                width: "49%",
              }}
              onAction={onSave}
            />
            <CreateButton
              label="download_certificate"
              customStyle={{
                width: "49%",
              }}
              onAction={onGeneratePdf}
            />
          </div>
        </UserMemberContentWrapper>
      </StaffStudentProfileContainer>
      {disabled && <QvipleLoader />}
      {certificateBonafideStudentDetailLoading && <QvipleLoader />}
      <Dialog open={openCrop}>
        <CustomCrop
          setOpenCrop={setOpenCrop}
          fileUrl={fileUrl}
          first={16}
          second={9}
          onCrop={onCrop}
        />
      </Dialog>
    </>
  );
};

export default BonafideStudentCertificateForm;
